<template>
  <ul class="list-group">

    <li v-for="todo in todos" class="list-group-item">
      <input type="checkbox" style="margin-right: 10px" @change="toggleCompleted(todo)"/>
      <span v-if="!todo.isFinished">
        {{todo.content}}
      </span>
      <span v-else>
        <s>{{todo.content}}</s>
      </span>
      <input type="button" @click="deleteTodo(todo)" class="btn btn-xs pull-right" value='x'/>
    </li>
  </ul>
</template>
<script>
  import bus from '../bus/bus'
  export default{
    name: "TodoList",
    props: ['todos'],
    data(){
      return {
      }
    },
    methods: {
      toggleCompleted(todo){
        todo.isFinished = !todo.isFinished
      },
      deleteTodo(todo){
          bus.$emit('deleteTodo',todo)
      }

    },
    components: {}
  };
</script>
<style scoped rel="stylesheet/scss">
</style>



