<template>
  <div class="well" style="width: 50%;margin: 0 auto">
    <Header title="TodoList via Vue"></Header>
    <TodoList :todos="todos"></TodoList>
    <Footer :result="result"></Footer>
    <TodoForm></TodoForm>
  </div>
</template>
<script>
  import bus from './bus/bus'
  import Header from './components/Header.vue'
  import TodoList from './components/TodoList.vue'
  import Footer from './components/Footer.vue'
  import TodoForm from './components/TodoForm.vue'
  export default {
    name: 'app',
    data(){
      return {
        todos: [
          {id: Date.now(), content: '吃饭', isFinished: false},
          {id: Date.now() + 1, content: '睡觉', isFinished: false},
          {id: Date.now() + 2, content: '打豆豆', isFinished: false}
        ]
      }
    },
    mounted(){
      bus.$on('deleteTodo', (todo) => {
        this.todos = this.todos.concat().filter(obj => obj.id !== todo.id)
      })
      bus.$on('addTodo', (content) => {
        this.todos = [...this.todos,{id: Date.now(), content: content, isFinished: false}]
      })
    },
    computed: {
      result() {
        return {
          total: this.todos.length,
          completed: (this.todos.concat().filter(todo => todo.isFinished !== false)).length
        }
      }
    },
    methods: {},
    components: {
      Header,
      TodoList,
      Footer,
      TodoForm
    }
  }
</script>

<style>
</style>



