<template>
  <div>
    <hr/>
    <form class="form-horizontal" @submit="addTodo">
      <div class="form-group">
        <label class="col-md-2 control-label">Task</label>
        <div class="col-md-10">
          <input type="text" ref="input" class="form-control" placeholder="你想做点什么"/>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12 text-right">
          <input type="submit" value="Save Task" class="btn btn-primary"/>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
  import bus from '../bus/bus'
  export default{
    name: "TodoForm",
    props: [],
    data(){
      return {
        msg: "bye world!"
      }
    },
    methods: {
      addTodo(e){
        e.preventDefault()
        bus.$emit('addTodo', this.$refs.input.value)
        this.$refs.input.value = ''
      }
    },
    components: {}
  };
</script>
<style scoped rel="stylesheet/scss">
</style>



